import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import Education from './education';
import Experience from './experience';
import Skills from './skills';
import avatar from './profile.jpg';

class About extends Component {
  render() {
      return(
        <div>
          <Grid>
            <Cell col={4}>
              <div style={{textAlign: 'center'}}>
                <img
                  src={avatar}
                  alt="avatar"
                  style={{height: '300px'}}
                   />
              </div>

              <h2 style={{paddingTop: '1em'}}>Deep Kumar</h2>
              <h4 style={{color: 'grey'}}>Programmer</h4>
              <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
              <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.</p>
              <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
              <h5>Address</h5>
              <p>Fairfax, VA</p>
              <h5>Phone</h5>
              <p>(571) 595-2107</p>
              <h5>Email</h5>
              <a href="mailto:dkumar7@gmu.edu"style={{color: 'black'}}>dkumar7@gmu.edu</a>
              <h5>Web</h5>
              <a href="https://dsahu040.github.io/"style={{color: 'black'}}>dsahu040.github.io</a>
              <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
            </Cell>
            <Cell className="resume-right-col" col={8}>
              <h2>Education</h2>


              <Education
                startYear={2019}
                endYear={2021}
                schoolName="George Mason University"
                schoolDescription="Master of Science in Computer Science"
                 />

                 <Education
                   startYear={2010}
                   endYear={2014}
                   schoolName="Rajiv Gandhi Technical University"
                   schoolDescription="B.E. in Electronics & Instrumentation Engineering"
                    />
                  <hr style={{borderTop: '3px solid #e22947'}} />

                <h2>Experience</h2>

                <Experience
                  startMonth={'Jan'}
                  startYear={2020}
                  endMonth={'May'}
                  endYear={2021}
                  jobName="Graduate Teaching Assistant"
                  employer='George Mason University'
                  />

              <Experience
                startMonth={'May'}
                startYear={2020}
                endMonth={'Aug'}
                endYear={2020}
                jobName="Software Development Engineer Intern"
                employer='Amazon Web Services, Seattle, USA'
                />

                <Experience
                  startMonth={'Jan'}
                  startYear={2018}
                  endMonth={'Jul'}
                  endYear={2019}
                  jobName="Senior Software Engineer"
                  employer='Allianz Technology, Pune, India'
                  />

                    <Experience
                      startMonth={'Dec'}
                      startYear={2014}
                      endMonth={'Jan'}
                      endYear={2018}
                      jobName="System Engineer"
                      employer='Tata Consultancy Services, Pune, India'
                      />


                <hr style={{borderTop: '3px solid #e22947'}} />
                <h2>Skills</h2>
                <Skills
                  skill="Java"
                  />
                  <Skills
                    skill="Spring Boot"
                    />
                    <Skills
                      skill="Web Services"
                      />
                      <Skills
                        skill="AWS"
                        />
            </Cell>
          </Grid>
        </div>
      )
    }
  }


export default About;
